import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import World from './views/World'
import vuespa from './views/VueSpa.vue'
import yousign from './views/Yousign.vue'
import grafico from './views/Grafico.vue'
import Gibson from './views/Gibson.vue'




Vue.use(Router)

export default new Router({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [  {
    path: '*',
    redirect: '/',

  },
    
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: { bodyClass: 'acceuil' },
   
    },
    {
      path: '/about',
      name: 'about',
      component: About,
      meta: { bodyClass: 'about' },
    },
    {
      path: '/gibson',
      name: 'gibson',
      component: Gibson,
      meta: { bodyClass: 'one' },
    },
    {
      path: '/world',
      name: 'world',
      component: World,
      meta: { bodyClass: 'one' },

    },
    {
      path: '/vuespa',
      name: 'vuespa',
      component: vuespa,
      meta: { bodyClass: 'two' },
    },
    {
      path: '/yousign',
      name: 'yousign',
      component: yousign,
      meta: { bodyClass: 'three' },
    },
    {
      path: '/grafico',
      name: 'grafico',
      component: grafico,
      meta: { bodyClass: 'four' },
    },
  
  ],

  
  mode: 'history'
  
})
